___CSS_LOADER_EXPORT___.push([module.id, `:root {
    box-sizing: border-box;
    --main-color: rgb(151, 191, 255);
    --main-color-darker: rgb(120, 172, 255);
    --font-color-menu: white;
    --checkbox-checked-color: rgb(255, 132, 18);
    --checkbox-checked-color-darker: rgb(244, 121, 6);
    --secondary-color: white;
    --secondary-color-darker: rgb(242, 241, 241);
}

body {
    display: grid;
    grid-template-columns: 200px 1fr;
    grid-template-rows: 1fr;
    height: 100vh;
    margin: 0;
    font-family: 'Roboto', sans-serif;
}

#menu {
    display: grid;
    grid-template-columns: 200px;
    grid-template-rows: 50px 40px 40px 40px;
    /* place-items: center; */
    justify-content: left;
    grid-column: 1;
    background-color: var(--main-color);
    box-shadow: 4px 0 10px rgba(0, 0, 0, 0.4);
    z-index: 999;
}

#menu>.dashboard {
    display: flex;
    justify-content: left;
    align-items: center;
    padding: 10px auto;
    background-color: var(--main-color);
    height: 50px;
    /* box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4); */
    border-bottom: 1px solid var(--font-color-menu);
    z-index: 999;
}

#menu>.dashboard>#dash {
    padding: 0px 6px;
    font-weight: 600;
    color: var(--font-color-menu);
    font-size: 1.5rem;
}

#menu>div {
    height: 40px;
}

#menu>div>button {
    width: 200px;
    text-align: left;
    border: none;
    height: 40px;
    padding: 0px 6px;
    background-color: var(--main-color);
    color: var(--font-color-menu);
    font-weight: 600;
    font-size: 1rem;
}

#menu>div>button:hover {
    cursor: pointer;
    /* box-shadow: 0 0 4px rgba(0, 0, 0, 0.4); */
    background-color: var(--main-color-darker);
}



#menu>div>button>i {
    margin-right: 5px;
}

#newCategoryDiv{
    position: absolute;
    top: 50px;
    right: 50px;
    width: 40px;
    height: 40px;
    background-color: var(--checkbox-checked-color);
    display: grid;
    place-items: center;
    z-index: 9999;
    border-radius: 5px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
}

#newCategoryDiv>#addCategoryBtn{
    font-size: 1.5rem;
    color: white;
    background-color: var(--checkbox-checked-color);
    border: none;
    width: 40px;
    height: 40px;
    border-radius: 5px;
}

#newCategoryDiv>#addCategoryBtn:hover{
    cursor: pointer;
    background-color: var(--checkbox-checked-color-darker);
}

#newCategoryDiv>#addCategoryBtn:active,
#newCategoryDiv:active{
    transform: scale(0.96);
}

#content {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 50px max-content;
    grid-column: 2;
    background-color: var(--main-color);
    z-index: 1;
    padding: 50px;
    row-gap: 30px;
}

#content>* {
    border-radius: 5px;
}

#content>.titleDiv {
    background-color: white;
    padding: 10px 0 10px 15px;
    display: flex;
    justify-content: left;
    align-items: center;
    width: clamp(200px, 250px, 300px);
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);

}

#content>.titleDiv>.title {
    font-size: 1.1rem;
    font-weight: 600;
}

#content>.container {
    background-color: white;
    width: clamp(500px, 600px, 1fr);
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
}

#content>.container>#categoryHeaderRow,
#content>.container>.item{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    height: 54px;
    align-items: center;
    border-bottom: 1px solid var(--main-color);
}

#content>.container>.item:last-child{
    border-bottom: none;
}

#content>.container>.item:last-child:hover{
    border-bottom: none;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}

#content>.container>#categoryHeaderRow{
    border: none;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
    font-weight: 600;
    z-index: 600;
    border-radius: 5px;
}

#content>.container>#categoryHeaderRow:hover{
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

#content>.container>.item{
    z-index: 500;
    display: grid;
    justify-content: left;
    align-items: center;
    grid-template-columns: 40px 1fr;
    font-weight: 600;

}

#content>.container>.item:hover{
    background-color: var(--secondary-color-darker);
    cursor: pointer;
    z-index: 400;
}

#content>.container>.item>.checkboxDiv>.fa-regular.fa-circle {
    color: var(--checkbox-checked-color);
}

#content>.container>.item>i{
    margin-right: 15px;
}

#content>.container>.item>i[class*="dumbbell"]{
    color: grey;
}

#content>.container>.item>i[class*="house"]{
    color: red;
}

#content>.container>.item>i[class*="building"]{
    color: blue;
}

#content>.container>.item>i[class*="beach"]{
    color: sandybrown;
}

#content>.container>.item>i[class*="book-bookmark"]{
    color: brown;
}

#content>.container>.item>i[class*="notes-medical"]{
    color: green;
}

#content>.container>div{
    padding: 0 15px 0 15px;
}


#content>.container>.item:hover #due{
    background-color: var(--secondary-color-darker);
}

#due{
    border: none;
}

#content>.container>.item>.checkboxDiv{
    display: grid;
    place-items: center;
}

#formDiv {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
    padding: 20px;
    background-color: var(--secondary-color);
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
    border-radius: 5px;
}

#formDiv>#form{
    display: flex;
    flex-direction: column;
    justify-content: left;

}

#formDiv>#form>div:nth-child(1){
    display: flex;
    flex-direction: column;
}

#formDiv>#form>div{
    margin-bottom: 10px;
    gap: 5px;
}

#formDiv>#form>div>label{
    font-weight: 500;
}

#formDiv>#form>div>input[type="text"]{
    border: none;
    height: clamp(16px, 20px, 24px);
    background-color: var(--secondary-color);
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);    
    border-radius: 5px;
    padding: 2px 5px;
}

#formDiv>#form>div>input:focus{
    outline: none;
}


#formDiv>#form>#submit{
    background-color: var(--checkbox-checked-color);
    border: none;
    border-radius: 5px;
    padding: 4px 5px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
    font-weight: 600;
    color: var(--secondary-color);
}

#formDiv>#form>#submit:hover{
    cursor: pointer;
    background-color: var(--checkbox-checked-color-darker);
}

#formDiv>#form>#submit:active{
    transform: scale(0.96);
}

#formDiv>#form>#closeFormBtn{
    position: absolute;
    top: 6px;
    right: 6px;
    color: var(--checkbox-checked-color);
    z-index: 1300;
    font-size: 10px;
}

#formDiv>#form>#closeFormBtn:hover{
    cursor: pointer;
    color: var(--checkbox-checked-color-darker);
}

#formDiv>#form>#closeFormBtn:active{
    transform: scale(0.96);
}`, "",{"version":3,"sources":["webpack://./src/styles/category.css"],"names":[],"mappings":"AAEA;IACI,sBAAsB;IACtB,gCAAgC;IAChC,uCAAuC;IACvC,wBAAwB;IACxB,2CAA2C;IAC3C,iDAAiD;IACjD,wBAAwB;IACxB,4CAA4C;AAChD;;AAEA;IACI,aAAa;IACb,gCAAgC;IAChC,uBAAuB;IACvB,aAAa;IACb,SAAS;IACT,iCAAiC;AACrC;;AAEA;IACI,aAAa;IACb,4BAA4B;IAC5B,uCAAuC;IACvC,yBAAyB;IACzB,qBAAqB;IACrB,cAAc;IACd,mCAAmC;IACnC,yCAAyC;IACzC,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,qBAAqB;IACrB,mBAAmB;IACnB,kBAAkB;IAClB,mCAAmC;IACnC,YAAY;IACZ,8CAA8C;IAC9C,+CAA+C;IAC/C,YAAY;AAChB;;AAEA;IACI,gBAAgB;IAChB,gBAAgB;IAChB,6BAA6B;IAC7B,iBAAiB;AACrB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,gBAAgB;IAChB,YAAY;IACZ,YAAY;IACZ,gBAAgB;IAChB,mCAAmC;IACnC,6BAA6B;IAC7B,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,eAAe;IACf,4CAA4C;IAC5C,0CAA0C;AAC9C;;;;AAIA;IACI,iBAAiB;AACrB;;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,WAAW;IACX,WAAW;IACX,YAAY;IACZ,+CAA+C;IAC/C,aAAa;IACb,mBAAmB;IACnB,aAAa;IACb,kBAAkB;IAClB,wCAAwC;AAC5C;;AAEA;IACI,iBAAiB;IACjB,YAAY;IACZ,+CAA+C;IAC/C,YAAY;IACZ,WAAW;IACX,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,sDAAsD;AAC1D;;AAEA;;IAEI,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,0BAA0B;IAC1B,oCAAoC;IACpC,cAAc;IACd,mCAAmC;IACnC,UAAU;IACV,aAAa;IACb,aAAa;AACjB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,uBAAuB;IACvB,yBAAyB;IACzB,aAAa;IACb,qBAAqB;IACrB,mBAAmB;IACnB,iCAAiC;IACjC,wCAAwC;;AAE5C;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,uBAAuB;IACvB,+BAA+B;IAC/B,wCAAwC;AAC5C;;AAEA;;IAEI,aAAa;IACb,0BAA0B;IAC1B,wBAAwB;IACxB,YAAY;IACZ,mBAAmB;IACnB,0CAA0C;AAC9C;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;IACnB,8BAA8B;IAC9B,+BAA+B;AACnC;;AAEA;IACI,YAAY;IACZ,wCAAwC;IACxC,gBAAgB;IAChB,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,2BAA2B;IAC3B,4BAA4B;AAChC;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,qBAAqB;IACrB,mBAAmB;IACnB,+BAA+B;IAC/B,gBAAgB;;AAEpB;;AAEA;IACI,+CAA+C;IAC/C,eAAe;IACf,YAAY;AAChB;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,sBAAsB;AAC1B;;;AA